<template>
  <div>

    <center>
      <div style="margin-top: 200px">
              <h2>Hbarbarian Club - NFT Rarity Checker</h2> <br/>


            Enter serial number
            <v-text-field name="serial" label="Ex: 100" id="account_id" style="width: 200px"
              type="account_id" v-model="inputSerial" v-on:blur="clearError()"
              outlined required single-line />
      </div>
      <div style="color:red">{{errMsg}}</div>

      <v-btn color="primary" @click="onClickSearch()" :disabled="false">
        Show
      </v-btn>

    </center>










    <v-dialog v-model="dialog"  width="500px">

      <v-card>

        <!-- <v-layout> -->
            <div style="width:100%">
              <v-img
                :src="this.imageURL" alt="loading..."/>
              <center>
                <h2>{{nft.displayName}}</h2>
                <i>{{nft.description}}</i><br/>

                [Rank: <b>  {{nft.rank}}</b>]<br/>
                [Rarity Score: <b>{{nft.rarity.toFixed(3)}}</b>]<br/>
              </center>

            </div>

            <div style="width:100%">
              
                    <v-data-table v-if="!nft.description.includes('special')" :headers="headers" :items="nft.traits.filter(x => {return x.score > 0})" class="elevation-1" :dark=false hide-actions >
                      <template v-slot:items="props">
                        <td>{{ props.item.name }}</td>
                        <td class="text-xs-right">{{ props.item.value }}</td>
                        <td class="text-xs-right">{{ props.item.occurrences }}</td>
                        <td class="text-xs-right">{{ Number(props.item.score).toFixed(3) }}</td>
                      </template>
                    </v-data-table>
            </div>

            <v-footer class="pa-3">
              <v-spacer></v-spacer>
              <div>&copy; {{ new Date().getFullYear() }} Hbarbarian Club </div>
            </v-footer>

            <!-- <div style="width:500px">
              <v-layout row wrap>
                <div v-for="trait in nft.traits.filter(x => {return x.score > 0})" :key="trait.name" style="width: 49%; background-color:#eef; margin: 0.5%; padding-left: 5px">
                    <span style="color:gray; font-size: 10px">{{trait.name}}</span><br/>
                    <span><b>{{trait.value}}</b></span>
                    <span style="color:blue">{{trait.occurrences}}</span>
                </div>
              </v-layout>
            </div> -->

        <!-- </v-layout> -->







            <!-- <v-flex xs12 sm12 md12 lg12>

              <v-card style='overflow:auto'>
                <v-layout column fill-height>
                  
                  <v-layout style="height:90%;" column>
                    <v-data-table :headers="headers" :items="nft.traits" class="elevation-1" :dark=false hide-actions >
                      <template v-slot:items="props">
                        <td>{{ props.item.name }}</td>
                        <td class="text-xs-right">{{ props.item.value }}</td>
                        <td class="text-xs-right">{{ props.item.occurrences }}</td>
                        <td class="text-xs-right">{{ props.item.score.toFixed(3) }}</td>
                      </template>
                    </v-data-table>


                  </v-layout>
                </v-layout>
              </v-card>

            </v-flex> -->






      </v-card>


    </v-dialog>
 
  </div>
</template>
<script>
import NFTService from '../service/NFTService';


  export default {
    data () {
      return {
        nftService: new NFTService(),

        dialogm1: '',
        dialog: false,
        inputSerial: '1',
        imageURL: '',
        errMsg: '',
        headers: [
          { text: 'TRAIT', align: 'left', sortable: false, value: 'name' },
          { text: 'VALUE', align: 'right', sortable: false, value: 'value' },
          { text: 'OCCURRENCES', align: 'right', sortable: false, value: 'occurrences' },
          { text: 'SCORE', align: 'right', sortable: false, value: 'score' },
        ],
        nft: {
          name: "Ogehi Hbarbarian #00055",
          description: "Ogehi of Hbarbarian Club",
          serial: 23,
          rarity: 23,
          rank: 23,
          traits: [
            { name: 'Background', value: 'Coco gradient background', occurrences: '100 (1%)', score: 24, },
            { name: 'Back', value: 'Back with axe', occurrences: '100 (1%)', score: 24, },
            { name: 'Body', value: 'Brown skin clear', occurrences: '100 (1%)', score: 24, },
            { name: 'Tattoo', value: 'Brown dragon tattoo', occurrences: '100 (1%)', score: 24, },
            { name: 'Mouth', value: 'Normal', occurrences: '100 (1%)', score: 24, },
            { name: 'Nose', value: "Bull's nose", occurrences: '100 (1%)', score: 24, },
            { name: 'Armor', value: 'None', occurrences: '100 (1%)', score: 24, },
            { name: 'Chestband', value: 'Mix steel chestband', occurrences: '100 (1%)', score: 24, },
            { name: 'Necklace', value: 'None', occurrences: '100 (1%)', score: 24, },
            { name: 'Wristband', value: 'Seven power wristband', occurrences: '100 (1%)', score: 24, },
            { name: 'Ears', value: 'Tiger t ears', occurrences: '100 (1%)', score: 24, },
            { name: 'Eyes', value: 'Kind eye', occurrences: '100 (1%)', score: 24, },
            { name: 'Sunglasses', value: 'Tecxy sunglasses', occurrences: '100 (1%)', score: 24, },
            { name: 'Beard', value: 'Hitbro beard', occurrences: '100 (1%)', score: 24, },
            { name: 'Hair', value: 'Red curls brown hair', occurrences: '100 (1%)', score: 24, },
            { name: 'Helmet', value: 'None', occurrences: '100 (1%)', score: 24, },
            { name: 'Antler', value: 'Cros unicorn antler', occurrences: '100 (1%)', score: 24, },
            { name: 'Weapon Left', value: 'Ball blade', occurrences: '100 (1%)', score: 24, },
            { name: 'Weapon Right', value: 'Three blades', occurrences: '100 (1%)', score: 24, },
          ]
        },

      }
    },
    methods: {
      onClickSearch () {
        var serial = ""
        if (this.inputSerial.includes("#")) {
          serial = Number(this.inputSerial.replace('#', '').trim())
        } else {
          serial = Number(this.inputSerial)
        }

        this.nftService.getNFTInfo(serial).then(data => {
          this.nft = data
          this.dialog = true
          this.imageURL = this.getImageURL()
        }).catch(e => {
          this.errMsg = "Invalid serial number. Please check and try again."
        })
      },
      getImageURL() {
        var url = `https://${this.nft.assetCID}.ipfs.dweb.link/${this.nft.name.replace(' ', '-')}.png`
        return url
      },
      clearError() {
        this.errMsg = ''
      }
    }
  }
</script>
<style lang="css">
  table.v-table tbody td, table.v-table tbody th {
      height: 25px;
  }
  .v-data-table__wrapper{height:100px !important;}

</style>


