import axios from 'axios';
const https = require('https');

export default class SocketService {
    getSocket() {
        const instance = axios.create({
            httpsAgent: new https.Agent({  
                rejectUnauthorized: false
            })
        });
    
        return instance;
    }

    getUnsafeAgent() {
        const httpsAgent = new https.Agent({
            rejectUnauthorized: false,
        })
        return httpsAgent
    }
}


